import { PageStore } from '@pdd/isomorphic-mobx-framework';
import { IPDDContext, getPDDContextValue } from 'src/framework/PDDContext';
import { createClientContext } from '@pdd/isomorphic-mobx-framework/lib/createClientContext';
import { observable, action } from 'mobx';
import { nanoid, changeTitle } from '@pdd/core-util';
import { getAccessToken, getUid } from '@pdd/auth';
import { getImmersiveData } from 'src/helper/immerse';
import { get } from 'lodash';
import navigation from '@pdd/navigation';
import { IAnyObject, IRequestContext } from 'src/types';
import BaseChildStore, { Item_Type_Key } from './BaseChildStore';

class BaseRootStore extends PageStore {
    $ctx: IPDDContext;

    $listId: string;

    // 是否回退缓存
    $isBackCache = false;

    $authError;

    /**
     * @description 页面标题
     */
    @observable $title: string;

    /**
     * @description 分享配置
     */
    @observable $shareConfig = {
        setNativeShare: true, // 是否启用原生分享
        setNativeShareButton: true, // 是否使用原生分享按钮
        shareInfo: {
            title: '',
            shareURL: '',
            message: '',
            thumbnailURL: '',
        },
        types: null,
        onShareClick: null,
        callBack: null,
    };

    prepareClientData = (): void => {};

    @action.bound
    updateShareConfig(config: IAnyObject): void {
        this.$shareConfig = { ...this.$shareConfig, ...config };
    }

    /**
     * @override
     */
    @action
    async initDataCallback(requestContext: IRequestContext): Promise<void> {
        const req = requestContext.__req;
        const res = requestContext.__res;
        this.$ctx = getPDDContextValue({ req, res, requestContext });
        //  ssr替换域名，防止超时
        if (process.env.NODE_ENV === 'development' && !process.env.BROWSER) {
            this.$axiosHttp.defaults.baseURL = 'https://api.pinduoduo.com';
        }
        if (!process.env.BROWSER && res) {
            if (!res.locals) {
                res.locals = {};
            }
            res.locals.isNativePlatform = this.$ctx.platform.isNativePlatform;
        }

        const query = this.$ctx.url.getQuery();
        this.$listId = this.$listId || query.list_id || nanoid(16);

        //  纯csr补充沉浸式参数
        if (process.env.BROWSER) {
            const needImmersive = query?.['_pdd_fs'] === '1';
            const [accessToken, uid, unoData] = await Promise.all([
                getAccessToken(),
                getUid(),
                needImmersive ? getImmersiveData(query) : null,
            ]);
            this.$ctx.accessToken = accessToken;
            this.$ctx.uid = uid;
            // @ts-ignore
            needImmersive && (this.$ctx.unoData = JSON.stringify(unoData));
        }

        return super.initDataCallback(requestContext).then((d) => {
            //  替换标题
            if (this.$title) {
                if (process.env.BROWSER) {
                    changeTitle(this.$title);
                } else if (this.$ctx.res && this.$ctx.res.locals) {
                    this.$ctx.res.locals.title = this.$title;
                }
            }
            return d;
        });
    }

    /**
     * 从js对象中填充数据，主要用于同构渲染的数据回填
     *
     * @param {Object} [rawData] 提供的状态回填数据
     */
    @action
    fromJS(rawData: IAnyObject, pageInfo: { pageId: string }): IAnyObject {
        const cstore: { [k: string]: BaseChildStore | BaseChildStore[] } = {};
        if (rawData) {
            // eslint-disable-next-line guard-for-in
            for (const k in this) {
                const v = this[k];
                if (v instanceof BaseChildStore) {
                    cstore[k] = v;
                } else if (v instanceof Array && ((v.length && v[0] instanceof BaseChildStore) || Item_Type_Key in v)) {
                    cstore[k] = v;
                }
            }
        }
        super.fromJS(rawData, pageInfo);
        const ctxData = get(rawData, ['$ctx'], null);
        this.$ctx = getPDDContextValue({
            data: ctxData,
            // @ts-ignore
            requestContext: createClientContext(),
        });
        // eslint-disable-next-line guard-for-in
        for (const k in cstore) {
            const v = cstore[k];
            if (v instanceof BaseChildStore) {
                this[k] = v.fromJS(this[k]);
            } else if (v instanceof Array && v.length && v[0] instanceof BaseChildStore) {
                // @ts-ignore
                this[k] = (this[k] || []).map((o) => new v[0].constructor(this).fromJS(o));
            } else if (v instanceof Array && Item_Type_Key in v) {
                // @ts-ignore
                this[k] = (this[k] || []).map((o) => new v[Item_Type_Key](this).fromJS(o));
            }
        }
        // listID
        const query = this.$ctx.url.getQuery();
        this.$listId = this.$listId || query.list_id || nanoid(16);
        if (!this.$ctx.platform.isNativePlatform) {
            navigation.replaceQuery({
                list_id: this.$listId,
            });
        }
        return this;
    }
}

export default BaseRootStore;
