import React, { useState, useEffect, useRef, useContext, memo } from 'react';
import ReactDOM from 'react-dom';
import ClientRender from '@components/ClientRender';
import { getScrollTop, setScrollTop } from 'src/helper/scroll';
import cx from 'classnames';

import s from './index.module.scss';

export default memo(function GoTop({ scrollContainer = null }) {
    const [isShow, setIsShow] = useState(false);
    const isShowRef = useRef(isShow);
    const marginBottomRef = useRef();
    isShowRef.current = isShow;
    const scroller = scrollContainer;

    useEffect(() => {
        const getBottomBarHeight = async () => {
            const height = await getBottomBarHeight();
            marginBottomRef.current = `${height}rem`;
        };
    }, []);

    useEffect(() => {
        const handleScroll = () => {
            const st = getScrollTop(scroller || document.body);
            if (st < window.innerHeight && isShowRef.current) {
                setIsShow(false);
            } else if (st >= window.innerHeight && !isShowRef.current) {
                setIsShow(true);
            }
        };
        const scrollerEl = scroller || window;
        scrollerEl.addEventListener('scroll', handleScroll);
        handleScroll();
        return () => {
            scrollerEl.removeEventListener('scroll', handleScroll);
        };
    }, [scroller]);
    if (!process.env.BROWSER) {
        return null;
    }
    return (
        <ClientRender>
            {ReactDOM.createPortal(
                <div
                    className={cx('bgt-go-top', s.goTop, {
                        [s.goTopShow]: isShow,
                        [s.goTopHide]: !isShow,
                    })}
                    onClick={() => {
                        const scrollContainer = scroller || document.body;
                        setScrollTop(0, scrollContainer);
                    }}
                    style={{ marginBottom: isShow ? marginBottomRef.current : undefined }}
                    role="button"
                >
                    <span>顶部</span>
                </div>,
                document.body
            )}
        </ClientRender>
    );
});
