import { getCurrentPlatform } from '@pdd/platform-util';
import { invokeBridgeCallNativeFun } from '@pdd/native-util';
import { throttle } from 'lodash';
import React, { useEffect, useRef, useState, useCallback, useContext } from 'react';
import { AppNavBarContext } from 'src/framework/Providers/AppNavBarContext';

export const useSticky = (
    navHeight = 0
): [boolean, React.Ref<HTMLDivElement>, React.Dispatch<React.SetStateAction<boolean>>] => {
    const [isSticky, setSticky] = useState<boolean>(false);
    const stickyRef = useRef<HTMLDivElement>(null);
    const scrollHandler = useCallback(
        throttle(() => {
            if (stickyRef.current) {
                const rect = stickyRef.current.getBoundingClientRect();
                setSticky(rect?.top <= navHeight);
            }
        }, 50),
        [setSticky, navHeight]
    );

    useEffect(() => {
        window.addEventListener('scroll', scrollHandler);
        scrollHandler();
        return () => {
            window.removeEventListener('scroll', scrollHandler);
        };
    }, [scrollHandler]);
    return [isSticky, stickyRef, setSticky];
};

const format = (time) => {
    return `${time}`.length < 2 ? `0${time}` : `${time}`;
};

const getTime = (timeDiff: number): [string, string, string] => {
    if (timeDiff <= 0) {
        return ['00', '00', '00'];
    }
    if (timeDiff > 100 * 3600) {
        return ['99', '59', '59'];
    }
    const hour = Math.floor(timeDiff / 3600);
    const minute = Math.floor((timeDiff - hour * 3600) / 60);
    const second = timeDiff - hour * 3600 - minute * 60;
    return [format(hour), format(minute), format(second)];
};

export const useCountDown = (timeDiff: number, callback?: () => void): [string, string, string] => {
    const [time, setTime] = useState(timeDiff);
    useEffect(() => {
        if (timeDiff) {
            setTime(timeDiff);
            const id = setInterval(() => {
                setTime((stateTime) => {
                    if (stateTime <= 0) {
                        clearInterval(id);
                        callback && callback();
                        return 0;
                    }
                    return stateTime - 1;
                });
            }, 1000);
            return () => clearInterval(id);
        }
    }, [timeDiff]);
    return getTime(time);
};

export const useImmerse = (): void => {
    const { isImmersive } = useContext(AppNavBarContext);
    useEffect(() => {
        const { isNativePlatform } = getCurrentPlatform();
        if (isNativePlatform) {
            invokeBridgeCallNativeFun('JSUIControl', 'setBackground', {
                background_color: '#db3929',
            });
        }
        if (isImmersive && isNativePlatform) {
            invokeBridgeCallNativeFun('JSUIControl', 'supportCustomNavigationBar').then(({ enable }) => {
                if (Number(enable)) {
                    invokeBridgeCallNativeFun('JSUIControl', 'setRollingAlpha', {
                        alpha: [0, 0],
                        offset: [0, 100],
                        btn_style: ['#ffffff', '#000000'],
                        status_bar_style: [1, 0],
                        height: 720,
                        continuous: 1,
                    });
                }
            });
        }
    }, []);
};
