import React from 'react';

const ListEnd = (props) => {
    if (!props.show) {
        return null;
    }
    return (
        <div
            style={{
                lineHeight: 1,
                fontWeight: 400,
                fontSize: '0.14rem',
                textAlign: 'center',
                color: '#666',
                padding: '0.2rem 0 0.4rem',
                ...props.style,
            }}
            className={props.className}
        >
            {props.text || '没有更多了...'}
        </div>
    );
};

export default ListEnd;
