// @ts-ignore
import { appendQuery, parseUrlQuery } from '@pdd/core-util';
// @ts-ignore
import { get } from 'lodash';
import { IAnyStringObject } from 'src/types';

export function getQuery(req?: Express.Request): IAnyStringObject {
    if (process.env.BROWSER) {
        return parseUrlQuery(location.href);
    }
    return get(req, 'query') || {};
}

export function buildURL(query: object, originalURL = '', origin?: string): string {
    if (process.env.BROWSER) {
        return appendQuery(query, originalURL) as string;
    }
    let url = new URL(originalURL, 'https://mobile.yangkeduo.com');
    if (origin) {
        const urlParts = url;
        url = new URL(origin);
        if (urlParts.pathname.length > 0) {
            url.pathname = urlParts.pathname;
        }
        if (urlParts.search.length > 0) {
            url.search = urlParts.search;
        }

        if (urlParts.hash.length > 0) {
            url.hash = urlParts.hash;
        }
    }
    for (const key of Object.keys(query || {})) {
        const value = query[key];
        if (value == null) {
            url.searchParams.delete(key);
        } else {
            url.searchParams.set(key, value);
        }
    }
    return url.href;
}
