import React from 'react';
import cx from 'classnames';

import s from './index.module.scss';

interface IProps {
    price: string | number;
    useYuan?: boolean;
    floatLength?: number;
    fontSize?: number | string;
    color?: string;
    yuanSize?: number | string;
    floatSize?: number | string;
    className?: string;
    intClassName?: string;
    yuanClassName?: string;
    floatClassName?: string;
    padFloat?: boolean;
}

const PDD_RED = '#E02E24';

const splitPrice = (price, floatLength = 2) => {
    const [intPart, floatPart = ''] = `${price}`.split('.');
    const slicedFloat = floatPart.slice(0, floatLength);
    return [intPart, slicedFloat];
};

const Price: React.FC<IProps> = (props: IProps) => {
    const {
        price,
        useYuan = true,
        color = PDD_RED,
        fontSize = '.12rem',
        floatLength = 2,
        yuanSize,
        floatSize,
        className,
        intClassName,
        floatClassName,
        yuanClassName,
        padFloat,
    } = props;
    const renderYuan = () => {
        return (
            useYuan && (
                <span className={cx(s.yuan, yuanClassName)} style={{ fontSize: yuanSize || fontSize }}>
                    ￥
                </span>
            )
        );
    };
    const renderInt = (priceText) => <span className={cx(intClassName)}>{priceText}</span>;
    const renderMultiSizePrice = () => {
        const [intPart, floatPart] = splitPrice(price, floatLength);
        const hasFloatPart = !!parseInt(floatPart, 10);
        return (
            <>
                {renderInt(intPart)}
                {hasFloatPart && (
                    <span className={cx(floatClassName)} style={{ fontSize: floatSize }}>
                        .{floatPart}
                    </span>
                )}
                {padFloat && !hasFloatPart && (
                    <span className={cx(floatClassName)} style={{ fontSize: floatSize }}>
                        .
                        {Array.from(new Array(floatLength))
                            .map((_) => '0')
                            .join('')}
                    </span>
                )}
            </>
        );
    };
    return price ? (
        <div className={cx(s.wrapper, className)} style={{ color, fontSize }}>
            {renderYuan()}
            {floatSize ? renderMultiSizePrice() : renderInt(price)}
        </div>
    ) : null;
};

export default Price;
