import React from 'react';
import { IRequestContext, IAnyStringObject, IAnyObject } from 'src/types';
import { Request, Response } from 'express';
import { buildQuery, httpsSafe, getCookie, throwErrorAsync } from '@pdd/core-util';
import { getQuery, buildURL } from 'src/helper/url';
import { app as config } from '@pdd/configs';
import { get } from 'lodash';
import getWebpEnable from 'src/helper/webp';
import { getPlatform, getCurrentPlatform, SYSTEM } from '@pdd/platform-util';
import { computed } from 'mobx';
import IPlatform from '@pdd/platform-util/lib/IPlatform';

export interface IURL {
    hostname: string;
    buildQuery(query: object): string;
    buildURL(query: object, originalURL: string, origin?: string): string;
    getQuery(): IAnyStringObject;
    getParameters(): IAnyStringObject;
    httpsSafe(url: string): string;
}

export class PDDContextValue {
    platform: IPlatform;

    requestContext: IRequestContext;

    url: IURL;

    webpEnable: boolean;

    req?: Request;

    res?: Response;

    isDEV = process.env.NODE_ENV !== 'production';

    isBrowser = !!process.env.BROWSER;

    unoData = null;

    accessToken: string;

    uid: string;

    config: IAnyObject = {};

    constructor({
        req,
        res,
        data = {},
        requestContext,
    }: {
        req?: Request;
        res?: Response;
        data: IAnyObject;
        requestContext: IRequestContext;
    }) {
        this.req = req;
        this.res = res;
        const platform = process.env.BROWSER ? getCurrentPlatform() : getPlatform(requestContext.userAgent);
        if (!process.env.BROWSER) {
            this.unoData = req.get('p-uno-context') || req.get('p-immerse');
        } else {
            this.unoData = get(data, ['unoData'], null); // 客户端渲染
            this.uid = get(data, 'uid');
            this.accessToken = get(data, 'accessToken');
        }
        this.requestContext = requestContext;
        this.platform = platform;
        this.webpEnable = getWebpEnable(req);
        this.url = {
            buildQuery,
            buildURL,
            getQuery: this.getQuery,
            getParameters: this.getQuery,
            httpsSafe,
            hostname: requestContext.hostname,
        };

        if (!process.env.BROWSER) {
            const AccessTokenKey = config.constant.CookieKey.AccessToken;
            this.accessToken = (req.cookies || {})[AccessTokenKey] || get(req, ['customStore', AccessTokenKey], null);

            const UIDKey = config.constant.CookieKey.UID;
            this.uid = (req.cookies || {})[UIDKey] || get(req, ['customStore', UIDKey], '') || '';
        }
    }

    getQuery = (): IAnyObject => {
        return getQuery(this.req);
    };

    @computed get isBack(): boolean {
        return this.getQuery().is_back === '1';
    }

    getAccessToken = (): string => {
        return this.accessToken;
    };

    getUIN = (): string => {
        const { req } = this;
        const UinKey = config.constant.CookieKey.UIN;
        return process.env.BROWSER ? localStorage.getItem(UinKey) || getCookie(UinKey) : (req.cookies || {})[UinKey];
    };

    getUID = (): string => {
        return this.uid;
    };

    @computed get isNativePlatform(): boolean {
        return this.platform.isNativePlatform;
    }

    @computed get isIOSSystem(): boolean {
        return this.platform.system === SYSTEM.IOS;
    }

    toJSON = (): { accessToken: string; uid: string | number; unoData: IAnyObject | null } => {
        return {
            accessToken: this.accessToken,
            uid: this.uid,
            unoData: this.unoData,
        };
    };

    sendError = (error: Error, name: string): void => {
        if (process.env.BROWSER) {
            throwErrorAsync(error);
        } else {
            const errorName = name || error?.message || '';
            if (this.req?.cat) {
                this.req.cat.logError(errorName, error);
            }
        }
    };
}

export type IPDDContext = PDDContextValue;

const PDDContext = React.createContext<IPDDContext>(null);

export default PDDContext;

let contextValue: IPDDContext;

export function getPDDContextValue({
    req,
    res,
    data = {},
    requestContext,
}: {
    req?: Request;
    res?: Response;
    data?: IAnyObject;
    requestContext: IRequestContext;
}): IPDDContext {
    if (!process.env.BROWSER && (!req || !req.get)) {
        return null;
    }
    if (contextValue) {
        return contextValue;
    }

    const result = new PDDContextValue({ req, res, data, requestContext });
    if (process.env.BROWSER) {
        contextValue = result;
    }
    return result;
}
