import React, { memo } from 'react';
import useSetScrollTop from 'src/hooks/replaceScrollTop';
import { LazyContainer } from '@pdd/lazy-image';
import WebpContext from './WebpContext';
import { AppNavBarProvider } from './AppNavBarContext';
import PDDContext, { IPDDContext } from '../PDDContext';

interface IProps {
    ctx: IPDDContext;
    children?: any;
}

const AppProviders = memo((props: IProps) => {
    const { ctx } = props;
    useSetScrollTop(ctx);
    return (
        <PDDContext.Provider value={ctx}>
            <LazyContainer rootMargin="1000px" rootRef={null}>
                <WebpContext.Provider value={ctx.webpEnable}>
                    <AppNavBarProvider>{props.children}</AppNavBarProvider>
                </WebpContext.Provider>
            </LazyContainer>
        </PDDContext.Provider>
    );
});

export default AppProviders;
