import { action, observable, computed, runInAction } from 'mobx';
import { BaseRootStore } from 'src/framework';
import dayjs from 'dayjs';
import { IAnyObject } from 'src/types';
import { priceCent } from '@pdd/core-util';
import Service, { removeDuplicate, formatGoodsData } from './service';

const count = 20;

const getNextDayDiff = (currentTime) => {
    return Math.floor((dayjs(currentTime).add(1, 'day').startOf('day').valueOf() - currentTime) / 1000);
};
export default class Store extends BaseRootStore {
    lowPriceService = new Service(this);

    bizPoolId = '';

    topGoods: '';

    isRedirect = false;

    @observable couponAmount = '';

    @observable timeDiff = 0;

    @observable topPrice = '';

    @observable goodsList = [] as IAnyObject[];

    @observable finishLoad = false;

    @observable isLoading = false;

    @observable offset = 0;

    getCoupon = async (): Promise<void> => {
        try {
            const { result = {} } = await this.lowPriceService.getCoupon({});
            const { discount, topPrice, bizPoolId, endTime } = result;
            const timeDiff = Math.floor(endTime - Date.now() / 1000);
            if (!result?.topPrice) {
                throw new Error('未命中人群');
            }
            runInAction(() => {
                this.couponAmount = discount ? priceCent(discount) : '';
                this.timeDiff = timeDiff || getNextDayDiff(Date.now());
                this.topPrice = priceCent(topPrice);
                this.bizPoolId = bizPoolId;
            });
        } catch (e) {
            runInAction(() => {
                this.isRedirect = true;
                this.couponAmount = '';
                this.timeDiff = getNextDayDiff(Date.now());
                this.topPrice = '';
                this.bizPoolId = '';
            });
        }
    };

    @action
    getLowPriceGoods = async (): Promise<void> => {
        if (!this.topPrice) {
            runInAction(() => {
                this.isLoading = false;
                this.finishLoad = true;
            });
            return;
        }
        if (this.isLoading || this.finishLoad) {
            return;
        }
        const params = {
            list_id: this.$listId,
            count,
            offset: this.offset,
            list_token: this.$ctx.requestContext?.query.top_goods,
            scene_id: `top_price_${this.topPrice}`,
        };
        this.isLoading = true;
        try {
            const { success, result = {}, errorCode } = await this.lowPriceService.getLowPriceGoods(params);
            if (!success) {
                return;
            }
            const { goodsList = [], hasMore } = result;
            runInAction(() => {
                if (!hasMore) {
                    this.finishLoad = true;
                }
                this.goodsList = removeDuplicate(
                    [...this.goodsList, ...formatGoodsData(goodsList)],
                    (goods) => goods.goodsId
                );
                this.isLoading = false;
                this.offset += count;
            });
        } catch (e) {
            this.isLoading = false;
            this.finishLoad = true;
        }
    };

    init = async (): Promise<void> => {
        await this.getCoupon();
        await this.getLowPriceGoods();
    };

    getDatasource(): () => Promise<void> {
        return this.init;
    }
}
