/**
 * @wiki /pages/viewpage.action?pageId=38929483
 */
import React, { useContext, useEffect, useState, memo } from 'react';
import { compareVersion } from '@pdd/platform-util';
import pinbridge from '@pdd/pinbridge';
import navigation from '@pdd/navigation';
import { navigationReplaceQueryNow } from 'src/helper/navigation';
import PDDContext from 'src/framework/PDDContext';

export const AppNavBarContext = React.createContext({
    isImmersive: false, // 是否要使用沉侵式全屏
    isSupportCustomNavigationBar: false, // 是否支持沉浸式
    canUseImmersive: false, // 是否支持沉浸式
    statusbarHeight: 0, // 导航栏高度
    navigationHeight: 0, // 状态栏高度
});

const parseUnoContext = (unoContextStr) => {
    if (unoContextStr) {
        try {
            return JSON.parse(unoContextStr);
            // eslint-disable-next-line
        } catch (e) {}
    }
};

export const AppNavBarProvider = memo(({ children }) => {
    const { url, platform, unoData } = useContext(PDDContext);
    const query = url.getQuery();
    const isTestImmersive = '__immersive' in query;
    const { immerse: canUseImmersive, nh, sh } = parseUnoContext(unoData) || {};

    const [statusbarHeight, setStatusbarHeight] = useState(canUseImmersive ? sh : isTestImmersive ? 20 : 0);
    const [navigationHeight, setNavigationHeight] = useState(canUseImmersive ? nh : isTestImmersive ? 64 : 0);
    const [isSupportCustomNavigationBar, setIsSupportCustomNavigationBar] = useState(!!canUseImmersive || false);

    const mayBeSupport =
        !!canUseImmersive || (platform.isNativePlatform && compareVersion(platform.version, '4.31.0') >= 0);
    const [isImmersive, setIsImmersive] = useState(isTestImmersive || (query._pdd_fs === '1' && mayBeSupport));

    useEffect(() => {
        if (canUseImmersive) {
            return;
        }

        if (isImmersive && platform.isNativePlatform) {
            pinbridge.callNative(
                'JSUIControl',
                'supportCustomNavigationBar',
                undefined,
                ({ enable }) => {
                    if (!Number(enable)) {
                        setIsImmersive(false);
                        navigationReplaceQueryNow({ _pdd_fs: null });
                    } else {
                        pinbridge.callNative(
                            'JSUIControl',
                            'getNavigationHeight',
                            {},
                            ({ navigation_height, statusbar_height }) => {
                                const statusbarHeight = parseInt(statusbar_height, 10) || 0;
                                const navigationHeight = parseInt(navigation_height, 10) || 0;
                                setStatusbarHeight(statusbarHeight);
                                setNavigationHeight(navigationHeight);
                            }
                        );
                        setIsSupportCustomNavigationBar(true);
                    }
                },
                () => {
                    setIsImmersive(false);
                    navigationReplaceQueryNow({ _pdd_fs: null });
                }
            );
        }

        if (isTestImmersive) {
            navigation.setNextPageQuery({
                __immersive: 1,
            });
        }
    }, []);
    return (
        <AppNavBarContext.Provider
            value={{
                isImmersive,
                statusbarHeight,
                navigationHeight,
                isSupportCustomNavigationBar,
                canUseImmersive: !!canUseImmersive,
            }}
        >
            {children}
        </AppNavBarContext.Provider>
    );
});

export function HideAppNavigationBar(props) {
    const ctx = useContext(PDDContext);
    const appBarContext = useContext(AppNavBarContext);

    useEffect(() => {
        if (ctx.platform.isNativePlatform) {
            if (appBarContext.isImmersive && appBarContext.isSupportCustomNavigationBar) {
                pinbridge.callNative('JSUIControl', 'hideNavigationBar');
            }
        }
    }, [appBarContext.isImmersive, appBarContext.isSupportCustomNavigationBar]);

    return null;
}
