import { sendMetrics as originSendMetrics, setDefaultParams } from '@pdd/metrics';
import { IAnyObject } from 'src/types';

const globalParams = {};

const sendMetrics = (params: IAnyObject, callback?: () => void): Promise<void> => {
    const cb = (...args) => {
        callback && callback(...args);
    };
    return originSendMetrics({
        ...params,
        ...globalParams,
    })
        .then(cb)
        .catch(cb);
};

export const setGlobalParams = (params: IAnyObject, cover = true): void => {
    if (cover === false) {
        setDefaultParams(params);
    } else {
        Object.assign(globalParams, params);
    }
};

export const sendPvMetrics = (params: IAnyObject, ...args: any[]): Promise<void> => {
    return sendMetrics(
        {
            op: 'pv',
            ...params,
        },
        ...args
    );
};

export const sendImprMetrics = (params: IAnyObject, ...args: any[]): Promise<void> => {
    return sendMetrics(
        {
            op: 'Impr',
            ...params,
        },
        ...args
    );
};

export const sendClickMetrics = (params: IAnyObject, ...args: any[]): Promise<void> => {
    return sendMetrics(
        {
            op: 'click',
            ...params,
        },
        ...args
    );
};

export default sendMetrics;
