import { observable } from 'mobx';
import { IPDDContext } from 'src/framework/PDDContext';
import { IAnyObject } from 'src/types';

const { hasOwnProperty } = Object.prototype;

export const Item_Type_Key = '__ItemType__';

function fromJS(target, rawData, ignore = false) {
    if (rawData) {
        for (const key in rawData) {
            if (hasOwnProperty.call(rawData, key)) {
                if (ignore && !(key in target)) {
                    continue;
                }
                const value = target[key];
                // eslint-disable-next-line @typescript-eslint/no-use-before-define
                if (value instanceof BaseChildStore) {
                    value.fromJS(rawData[key], ignore);
                } else if (
                    value instanceof Array &&
                    value.length &&
                    // eslint-disable-next-line @typescript-eslint/no-use-before-define
                    value[0] instanceof BaseChildStore
                ) {
                    target[key] = (rawData[key] || []).map((o) => new value[0].constructor(target).fromJS(o, ignore));
                } else if (value instanceof Array && Item_Type_Key in value) {
                    target[key] = (rawData[key] || []).map((o) => new value[Item_Type_Key](target).fromJS(o, ignore));
                } else {
                    target[key] = rawData[key];
                }
            }
        }
    }
    return target;
}

export default class BaseChildStore {
    static list() {
        const list = observable.array([]);
        list[Item_Type_Key] = this;
        return list;
    }

    getParent: () => IAnyObject | BaseChildStore;

    constructor(parent: IAnyObject | BaseChildStore) {
        this.getParent = () => parent;
    }

    get $parent() {
        return this.getParent();
    }

    get $root(): IAnyObject {
        return this.$parent.$root || this.$parent;
    }

    get $ctx() {
        return this.$root.$ctx as IPDDContext;
    }

    /**
     * 从js对象中填充数据，主要用于同构渲染的数据回填
     * @param {Object} [rawData] 提供的状态回填数据
     */
    fromJS(rawData, ignore = false) {
        return fromJS(this, rawData, ignore);
    }
}
