import { Request } from 'express';
import { getCookie } from '@pdd/core-util';

const COOKIE_KEY = 'webp';
const COOKIE_VALUE_TRUE = '1';

let webpEnable = false;

export default (req?: Request) => {
    if (webpEnable) {
        return true;
    }
    if (process.env.BROWSER) {
        webpEnable = getCookie(COOKIE_KEY) === COOKIE_VALUE_TRUE;
        return webpEnable;
    }

    return req.cookies[COOKIE_KEY] === '1';
};
