import React, { useContext } from 'react';
import PDDContext from 'src/framework/PDDContext';
import { FirstScreen as CMTFirstScreen } from '@pdd/monitor';
import { isIOS } from 'src/helper/ua';

export default function FirstScreen() {
    const { requestContext } = useContext(PDDContext);
    return (
        <>
            {isIOS(requestContext.userAgent) && (
                <div
                    // eslint-disable-next-line react/no-danger
                    dangerouslySetInnerHTML={{
                        __html: `<div style="height: 0">${'\u200b'.repeat(300)}</div>`,
                    }}
                />
            )}
            <CMTFirstScreen />
        </>
    );
}
