import { BaseService } from 'src/framework/BaseService';
import { IAnyObject } from 'src/types';
import { priceCent } from '@pdd/core-util';

export const removeDuplicate = (arr: IAnyObject[] = [], getKey: (IAnyObject) => string): IAnyObject[] => {
    const keysMap = {};
    const newArr = [] as IAnyObject[];
    for (let i = 0; i < arr.length; i++) {
        const key = getKey(arr[i]);
        if (!keysMap[key]) {
            newArr.push(arr[i]);
            keysMap[key] = true;
        }
    }
    return newArr;
};

export function formatGoodsData(result: IAnyObject): IAnyObject[] {
    return result.map((item) => {
        const {
            goodsId,
            shortName,
            goodsName,
            hdThumbUrl,
            imageUrl,
            price,
            priceInfo,
            linkUrl,
            tagList,
            iconList,
            salesTip,
            specialText,
            priceType,
            nearGroup,
        } = item;
        return {
            goodsId,
            goodsName: shortName || goodsName,
            imageUrl: imageUrl || hdThumbUrl,
            price: priceCent(price),
            priceUsingCoupon: priceType === 2 ? priceInfo : '',
            linkUrl,
            tagList: tagList || [],
            iconList: iconList || [],
            salesTip: salesTip || '',
            pRec: item.pRec ? JSON.stringify(item.pRec) : '',
            specialText: specialText || null,
            avatarList: nearGroup?.list?.length ? nearGroup.list : [],
        };
    });
}

export default class LowPriceService extends BaseService {
    getCoupon = (params: IAnyObject): Promise<IAnyObject> => {
        return this.post('/api/avengers/nmpu/coupon_info', params, {
            defaultErrorHandler: () => {},
        });
    };

    getLowPriceGoods = (params: IAnyObject): Promise<IAnyObject> => {
        return this.post('/api/avengers/nmpu/goods_list', params, {
            ignoreTransferKeys: ['p_rec'],
            defaultErrorHandler: () => {},
        });
    };
}
