import React, { Component } from 'react';
import { observer, inject } from 'mobx-react';
import { AppProviders } from 'src/framework';
import ClientRender from '@components/ClientRender';
import GoTop from '@components/GoTop';
import { appProvider } from '@pdd/xRender/isoAdapter';
import { useLeoKeys as injectLeoKeys } from '@pdd/leo-adaptor';
import sendMetrics, { setGlobalParams } from 'src/helper/metrics';
import navigation from '@pdd/navigation';
import { navigationForward } from 'src/helper/navigation';
import { login, getUid } from '@pdd/auth';
import { invokeBridgeCallNativeFun } from '@pdd/native-util';
import { getCurrentPlatform } from '@pdd/platform-util';

import leoConfig from './leo';

import Store from './store';
import Main from './modules';

import './index.scss';

const pageSn = '113831';

// @ts-ignore
@injectLeoKeys(leoConfig)
@appProvider(
    { store: Store },
    {
        requireLogin: true,
        serverTemplateName: 'chl_low_price',
        fallbackCSR: true,
        title: '低价模块',
        options: {
            pageProperty: {
                pageSN: pageSn,
                pageName: 'chl_low_price',
                noReportPv: true,
            },
            shareInfo: {
                disableShare: true,
            },
            noDefaultShare: true,
        },
    }
)
@inject('store')
@observer
export default class LowPrice extends Component<{ store: Store }> {
    get store() {
        return this.props.store;
    }

    componentDidMount(): void {
        setGlobalParams(
            {
                goods_type: this.store.topPrice || '',
                with_coupon: this.store.couponAmount ? '1' : '0',
            },
            false
        );
        sendMetrics({
            op: 'pv',
        });
        navigation.setNextPageQuery({
            refer_page_sn: pageSn,
        });
        if (this.store.isRedirect) {
            navigationForward('/index.html');
        } else {
            this.checkLogin();
        }
    }

    checkLogin = async () => {
        const uid = await getUid().catch(() => 0);
        if (!uid) {
            login();
        } else if (getCurrentPlatform().isNativePlatform) {
            invokeBridgeCallNativeFun(
                'PDDTitanPush',
                'register',
                {
                    biz_type: 10082,
                    push_receiver: () => {
                        location.reload();
                    },
                },
                '4.73.0'
            );
        }
    };

    render() {
        const { store } = this;
        return (
            <AppProviders ctx={store.$ctx}>
                <Main />
                <ClientRender>
                    <GoTop />
                </ClientRender>
            </AppProviders>
        );
    }
}
