import { AxiosRequestConfig } from 'axios';

import getAntiContent from 'src/helper/getAntiContent';
import { errors } from '@pdd/error-status';
import { IAnyObject } from '../types/index';
import BaseChildStore from './BaseChildStore';
import BaseRootStore from './BaseRootStore';

/**
 * aB => a_b 驼峰转化
 * @param name
 * @returns {void|XML|string|*}
 */
function underLine(key) {
    return key
        .replace(/([A-Z]+)/g, function (_, $1) {
            return `_${$1.toLowerCase()}`;
        })
        .replace(/^_/, '');
}

export function camel2underline(obj: IAnyObject): IAnyObject {
    if (!obj) {
        return obj;
    }
    const map = {};
    Object.keys(obj).forEach((key) => {
        if (!obj.hasOwnProperty(key)) {
            return;
        }
        const underLineKey = underLine(key);
        map[underLineKey] = obj[key];
    });
    return map;
}

/**
 * a_b => aB 驼峰转化
 * @param name
 * @returns {void|XML|string|*}
 */
function camelName(name) {
    const re = /_(\w)/g;
    return name.replace(re, function (_, $1) {
        return $1.toUpperCase();
    });
}

/**
 * 转化格式
 * @param data
 * @param result
 * @param {string[]} [ignoreTransferKeys]
 * @returns {*|{}}
 */
export function transfer(data, result, ignoreTransferKeys: string[] = []) {
    result = result || {};

    for (const key in data) {
        if (data.hasOwnProperty(key)) {
            if (typeof data[key] === 'object' && data[key] !== null && ignoreTransferKeys.indexOf(key) === -1) {
                result[camelName(key)] = transfer(data[key], Array.isArray(data[key]) ? [] : {}, ignoreTransferKeys);
            } else {
                result[camelName(key)] = data[key];
            }
        }
    }
    return result;
}

export interface IServiceRequestOptions extends AxiosRequestConfig {
    enableAntiContent?: boolean;
    ignoreTransferKeys?: string[];
    disableTransferData?: boolean;
    encodeNativeResponse?: boolean;
    disableParamsCamel2underline?: boolean;
    defaultErrorHandler?: () => any;
}

function abortWhenTimeout(request, timeout) {
    return Promise.race([
        request,
        new Promise((_, reject) => setTimeout(() => reject(new Error(`timeout of ${timeout}`)), timeout)),
    ]);
}

function handlerAuthError(error, root: BaseRootStore) {
    // 如果是服务端未登录错误或风控错误，就存入store,交给客户端处理
    if (!process.env.BROWSER) {
        const errorCode = parseInt(error.errorCode, 10);
        if (errorCode === errors.AUTHENTICATION_FAILED) {
            root.$authError = {
                errorCode,
                response: {
                    data: error?.response?.data,
                },
            };
        }
    }

    // 为了不影响现有逻辑，错误还是抛给外面处理
    throw error;
}

export class BaseService extends BaseChildStore {
    async get(
        url,
        params = {} as IAnyObject,
        {
            timeout,
            enableAntiContent = false,
            ignoreTransferKeys = [],
            disableTransferData = false,
            ...otherConfig
        } = {} as IServiceRequestOptions
    ) {
        if (enableAntiContent) {
            params = { ...params };
            if (process.env.BROWSER) {
                params.antiContent = await getAntiContent.getAntiContent();
            } else {
                params.anti_robot_node = '1';
            }
        }

        const request = this.$root.$axiosHttp
            .get(url, { params: camel2underline(params), timeout, ...otherConfig })
            .then((data) => {
                if (disableTransferData) {
                    return data;
                }
                return transfer(data, {}, ignoreTransferKeys);
            })
            .catch((err) => handlerAuthError(err, this.$root));
        if (timeout && this.$ctx.platform.isNativePlatform) {
            return abortWhenTimeout(request, timeout);
        }
        return request;
    }

    async post(
        url,
        params = {} as IAnyObject,
        {
            timeout,
            enableAntiContent = false,
            ignoreTransferKeys = [],
            disableTransferData = false,
            disableParamsCamel2underline = false,
            ...otherConfig
        } = {} as IServiceRequestOptions
    ) {
        if (enableAntiContent) {
            params = { ...params };
            params = { ...params };
            if (process.env.BROWSER) {
                params.antiContent = await getAntiContent.getAntiContent();
            } else {
                params.anti_robot_node = '1';
            }
        }
        let requestParams = params;
        if (!disableParamsCamel2underline) {
            requestParams = camel2underline(params);
        }
        const request = this.$root.$axiosHttp
            .post(url, requestParams, { timeout, ...otherConfig })
            .then((data) => {
                if (disableTransferData) {
                    return data;
                }
                return transfer(data, {}, ignoreTransferKeys);
            })
            .catch((err) => handlerAuthError(err, this.$root));
        if (timeout && this.$ctx.platform.isNativePlatform) {
            return abortWhenTimeout(request, timeout);
        }
        return request;
    }
}
