import React, { useCallback } from 'react';
import { observer, inject } from 'mobx-react';
import Div from '@components/Div';
import LazyImage from '@components/LazyImage';
import cx from 'classnames';
import { IAnyObject } from 'src/types';
import Sentinel from '@components/List/Sentinel';
import ListLoading from '@components/List/ListLoading';
import ListEnd from '@components/List/ListEnd';
import { appendQuery } from '@pdd/core-util';
import { TipOnImage } from '@pdd/bui-goods/lib/TipOnImage';
import { navigationForward } from 'src/helper/navigation';
import { sendClickMetrics } from 'src/helper/metrics';
import Impr from '@pdd/easy-impr';
import Price from '@components/Price';
import Store from '../../store';

import s from './index.iso.scss';

interface IItem {
    imgUrl: string;
    goodsName: string;
    goodsId: number;
    shortName?: string;
    tagList: Array<ITags>;
    iconList: Array<IAnyObject>;
    price: string;
    priceUsingCoupon?: string;
    salesTip: string;
    imageUrl: string;
    linkUrl: string;
    pRec?: IAnyObject;
    specialText?: IAnyObject;
    avatarList?: Array<IAnyObject>;
}

interface ITags {
    text?: string;
    textColor?: string;
    url?: string;
}

const Item = ({ goods, idx }: { goods: IItem; idx: number }) => {
    const {
        iconList,
        priceUsingCoupon,
        price,
        linkUrl,
        salesTip,
        imageUrl,
        pRec,
        goodsName,
        goodsId,
        specialText,
        avatarList = [],
    } = goods;
    const [avatar1 = '', avatar2 = ''] = avatarList.slice(0, 2).map((item) => item?.avatar);
    const renderTags = useCallback((tags) => {
        return tags.map((item, index) => {
            const { text = '', textColor = '#f4f4f4', url = '' } = item;
            return (
                <div className={cx(s.tag, { [s.tagWithImg]: url })} key={index} style={{ color: textColor }}>
                    {url && <Div backgroundImage={url} className={s.imgTag} />}
                    <span>{text}</span>
                </div>
            );
        });
    }, []);

    const handleClick = useCallback(async (e, link, trackingParams) => {
        e.stopPropagation();
        await sendClickMetrics(trackingParams);
        navigationForward(appendQuery({ refer_page_el_sn: 7119237 }, link));
    }, []);

    const trackingParams = {
        page_el_sn: 7119237,
        p_rec: pRec,
        rec_goods_id: pRec ? goodsId : null,
    };
    return (
        <Impr trackingInfo={trackingParams}>
            <div
                className={s.itemWrap}
                onClick={(e) => {
                    handleClick(e, linkUrl, trackingParams);
                }}
            >
                <div className={s.imgWrap}>
                    <LazyImage src={imageUrl} lazy={idx > 5} className={s.itemImg} />
                    {specialText && <TipOnImage data={specialText} />}
                </div>
                <div className={s.nameWrap}>
                    {!!iconList.length &&
                        iconList.map((icon, index) => {
                            const { url = '', width, height } = icon;
                            const resizeWidth = parseFloat(((width / height) * 0.14).toFixed(4)) || 0.52;
                            return (
                                <LazyImage
                                    key={index}
                                    src={url}
                                    className={s.icon}
                                    lazy
                                    style={{ width: `${resizeWidth}rem` }}
                                />
                            );
                        })}
                    <span className={s.goodsName}>{goodsName}</span>
                </div>
                <div className={s.goodsTag}>{!!goods.tagList && renderTags(goods.tagList)}</div>
                <div className={s.priceWrap}>
                    {priceUsingCoupon ? <div className={s.coupon}>券后</div> : null}
                    <Price
                        className={s.price}
                        price={priceUsingCoupon || price}
                        fontSize=".17rem"
                        yuanSize=".12rem"
                        floatSize=".13rem"
                    />
                    <div className={s.text}>{salesTip}</div>
                    {avatar1 && <LazyImage className={s.avatar1} src={avatar1} />}
                    {avatar2 && <LazyImage className={s.avatar2} src={avatar2} />}
                </div>
            </div>
        </Impr>
    );
};

const GoodsList = observer(({ store }: { store: Store }) => {
    const { getLowPriceGoods, goodsList } = store;
    return (
        <>
            <div className={s.listWrap}>
                {goodsList.map((item, index) => {
                    return <Item goods={item} key={item.goodsId} idx={index} />;
                })}
            </div>
            {store.isLoading && <ListLoading show />}
            {!store.finishLoad && <Sentinel onIntersecting={getLowPriceGoods} />}
            {store.finishLoad && <ListEnd show />}
            <div style={{ width: '100%', height: '.45rem' }} />
        </>
    );
});

export default inject('store')(GoodsList);
