import React, { useCallback, useContext, useRef } from 'react';
import Div from '@components/Div';
import cx from 'classnames';
import { AppNavBarContext } from 'src/framework/Providers/AppNavBarContext';
import { inject, observer } from 'mobx-react';
import Price from '@components/Price';
import Impr from '@pdd/easy-impr';
import { useSticky, useCountDown, useImmerse } from '../../hook';
import Store from '../../store';

import s from './index.module.scss';

const BG_IMG = 'https://promotion.pddpic.com/upload/domino/activity/63b0019a-a2eb-4646-88e9-d9c8be3c3939.png.slim.png';
const RED_ENVELOPE =
    'https://promotion.pddpic.com/upload/domino/activity/3eacdf4c-40a2-458a-9aac-95e9956c2557.png.slim.png';

const Head = ({ store }: { store: Store }) => {
    useImmerse();
    const wrapRef = useRef<HTMLDivElement>(null);
    const stickWrapRef = useRef<HTMLDivElement>(null);
    const { topPrice, couponAmount, timeDiff } = store;
    const [isSticky, stickyRef, setSticky] = useSticky();
    const [hour, minute, second] = useCountDown(timeDiff, () => {
        location.reload();
    });
    const navContext = useContext(AppNavBarContext);
    const statusbarHeight = navContext.statusbarHeight || 0;
    const stkCls = cx(s.stickyHead, {
        [s.stickyHeadShow]: isSticky,
    });
    const wrapCls = cx(s.wrap, {
        [s.warpSamll]: !couponAmount,
    });
    const handleClickUseButton = useCallback((e) => {
        e.stopPropagation();
        if (wrapRef.current && stickWrapRef.current) {
            const { height } = wrapRef.current.getBoundingClientRect();
            const { height: stickyHeight } = stickWrapRef.current.getBoundingClientRect();
            window.scrollTo(0, height - stickyHeight + 2);
        }
        setSticky(true);
    }, []);
    return (
        <>
            <div className={wrapCls} ref={wrapRef}>
                <Div className={s.bg} backgroundImage={BG_IMG} />
                <div className={s.topTitle}>
                    <span>全场低价</span>
                    {topPrice && <span className={s.topAmount}>{topPrice}元封顶</span>}
                </div>
                <div className={s.countdown}>
                    <span className={s.countText}>距离活动结束仅剩</span>
                    <div className={s.block}>{hour}</div>
                    <div className={s.colon}>:</div>
                    <div className={s.block}>{minute}</div>
                    <div className={s.colon}>:</div>
                    <div className={s.block}>{second}</div>
                </div>
                {couponAmount && (
                    <Impr trackingInfo={{ page_el_sn: 7119238, coupon_amount: couponAmount }}>
                        <div className={s.couponWrap}>
                            <div className={s.coupon}>
                                <Div className={s.couponImg} backgroundImage={RED_ENVELOPE} />
                                <div className={s.couponText}>
                                    你有
                                    <span className={s.couponAmount}>{couponAmount}元无门槛</span>平台券待使用
                                </div>
                                <div className={s.couponButton} onClick={handleClickUseButton}>
                                    立即使用
                                </div>
                            </div>
                        </div>
                    </Impr>
                )}
                <div ref={stickyRef} className={cx(s.stickyGuard, { [s.stickyGuardSmall]: !couponAmount })} />
            </div>
            <div className={stkCls} style={{ paddingTop: statusbarHeight }} ref={stickWrapRef}>
                <div className={s.title}>
                    全场低价
                    {topPrice && <span className={s.stickyTopAmount}>{topPrice}元封顶</span>}
                </div>
                {couponAmount && (
                    <div className={s.hint}>
                        <Div backgroundImage={RED_ENVELOPE} className={s.stickyCpImg} />
                        <div className={s.stickyTextWrap}>
                            <Price padFloat price={couponAmount} fontSize=".13rem" color="#E02E24" floatSize=".13rem" />
                            <span>无门槛券待使用，距活动结束仅剩</span>
                            <span className={s.stickyCount}>
                                {hour}:{minute}:{second}
                            </span>
                        </div>
                    </div>
                )}
            </div>
        </>
    );
};

export default inject('store')(observer(Head));
