/**
 * RiskController.js
 * @file RiskController.js
 */
import RiskControlAnti from '@pdd/risk-control-anti';
import { throwErrorAsync } from '@pdd/core-util';

const isClientEnv = process.env.BROWSER;

let instance = null;
class RiskController {
    constructor(opts = {}) {
        this.debug = process.env.NODE_ENV === 'development';
        this.serverTime = opts.serverTime;
        this.riskControlCrawler = null;
    }

    initRiskController() {
        if (!this.riskControlCrawler) {
            this.riskControlCrawler = new RiskControlAnti({
                serverTime: this.serverTime,
                _2827c887a48a351a: false,
            });
            this.riskControlCrawler.init();
        }
    }

    getRiskControlInfoSync() {
        if (!this.riskControlCrawler) {
            return Promise.resolve(null);
        }
        return this.riskControlCrawler
            .messagePackSync({
                touchEventData: true,
                clickEventData: true,
                focusblurEventData: true,
                changeEventData: true,
                locationInfo: true,
                referrer: true,
                browserSize: true,
                browserInfo: true,
                token: true,
                fingerprint: true,
            })
            .catch((err) => {
                throwErrorAsync(err);
                return null;
            });
    }

    clearCache() {
        this.riskControlCrawler && this.riskControlCrawler.clearCache();
    }

    getRiskControlInfoAsync() {
        return new Promise((res, rej) => {
            this.initRiskController();
            res(this.getRiskControlInfoSync());
        });
    }
}

RiskController.getInstance = function (opts) {
    if (!isClientEnv) {
        return null;
    }
    if (!instance) {
        instance = new RiskController(opts);
    }
    return instance;
};

export default RiskController;
