import { useEffect } from 'react';
import { NavigationActions, navigationEmitter, navigationReplaceQuery } from 'src/helper/navigation';
import { get } from 'lodash';
import { getBodyScrollTop } from '@pdd/core-util';

export default (ctx) => {
    useEffect(() => {
        const setSp = () => {
            if (!ctx.platform.isNativePlatform) {
                navigationReplaceQuery({
                    sp: get(window, 'Navigation.querySet.sp') || getBodyScrollTop(),
                });
            }
        };
        navigationEmitter.on(NavigationActions.Forward, setSp);
        return () => navigationEmitter.off(NavigationActions.Forward, setSp);
    }, []);
};
