import { invokeBridgeCallNativeFun } from '@pdd/native-util';

export function getImmersiveData(query) {
    const FALLBACK_DATA = {
        immerse: 0,
        sh: 0,
        nh: 0,
    };
    const IMMERSIVE_QUERY_KEY = '_pdd_fs';

    return invokeBridgeCallNativeFun('JSUIControl', 'supportCustomNavigationBar')
        .then(async ({ enable }) => {
            if (!Number(enable)) {
                return FALLBACK_DATA;
            }

            let data;
            try {
                data = await invokeBridgeCallNativeFun('JSUIControl', 'getNavigationHeight');
            } catch (e) {
                return FALLBACK_DATA;
            }

            const { navigation_height: navigationHeight, statusbar_height: statusbarHeight } = data || {};

            return {
                immerse: query?.[IMMERSIVE_QUERY_KEY] === '1' ? 1 : 0,
                sh: parseInt(statusbarHeight, 10) || 0,
                nh: parseInt(navigationHeight, 10) || 0,
            };
        })
        .catch(() => {
            return FALLBACK_DATA;
        });
}
