import React from 'react';

if (process.env.BROWSER) {
    require('intersection-observer');
}

// 哨兵
class Sentinel extends React.Component {
    static defaultProps = {
        rootMargin: '600px',
    };

    rootRef = React.createRef();

    componentDidMount() {
        this.io = new IntersectionObserver(this.observerCallBack, {
            root: this.props.root,
            rootMargin: this.props.rootMargin,
            thresholds: this.props.thresholds,
        });
        this.io.observe(this.rootRef.current);
    }

    componentWillUnmount() {
        this.io.disconnect();
    }

    observerCallBack = (entries) => {
        if (entries[0].isIntersecting && this.props.onIntersecting) {
            this.props.onIntersecting(entries[0]);
        }
    };

    render() {
        return (
            <div
                ref={this.rootRef}
                style={{
                    height: 1,
                    ...this.props.style,
                }}
            />
        );
    }
}

export default Sentinel;
