import React from 'react';
import cx from 'classnames';

import styles from './ListLoading.module.scss';

const ListLoading = (props) => {
    if (!props.show) {
        return null;
    }
    return (
        <div
            className={cx(styles.listLoading, { [styles.loadingWhite]: props.isWhite }, props.className)}
            style={props.style}
        >
            <span className={styles.loadingIcon} style={props.iconStyle} />
            <span className={styles.loadingText} style={props.textStyle}>
                正在加载中...
            </span>
        </div>
    );
};

export default ListLoading;
