import navigation, { isWxAppSupportForward } from '@pdd/navigation';
import { appendQuery, parseUrlQuery } from '@pdd/core-util';
import { EventEmitter } from 'events';
import { getCurrentPlatform } from '@pdd/platform-util';

export const navigationEmitter = new EventEmitter();

/** @enum {string} */
export const NavigationActions = {
    Forward: 'forward',
};

//  复写naviation
if (process.env.BROWSER) {
    const platform = getCurrentPlatform();
    const uniformForward = navigation.uniformForward.bind(navigation);
    navigation.uniformForward = (url, ...args) => {
        navigationEmitter.emit(NavigationActions.Forward);
        //  小程序不支持history api, 暂时支持,不支持跳小程序自己实现的页面，不然点击不动，需要点两下
        if (
            (platform.isWeChatMiniProgram && !isWxAppSupportForward(url && url[0] === '/' ? '' : `/${url}`)) ||
            platform.isQQMiniProgram
        ) {
            if (!navigation.querySet) {
                navigation.querySet = {};
            }
            navigation.querySet.is_back = '1';
            navigation.replaceURL(appendQuery(navigation.querySet, location.href));
        }
        uniformForward(url, ...args);
    };
    navigation.forward = (url) => {
        navigation.uniformForward(url);
    };
}

export function navigationUniformForward(url, params) {
    return navigation.uniformForward(url, undefined, params);
}

/**
 * 跳转
 * @param {string} url
 * @param {object} [params]
 */
export function navigationForward(url, params?) {
    return navigationUniformForward(url, params);
}

export function navigationReplaceQuery(query = {}) {
    return navigation.replaceQuery(query);
}

export function navigationSetNextPageQuery(query = {}) {
    return navigation.setNextPageQuery(query);
}

export function navigationReplaceQueryNow(query = {}) {
    return navigation.replaceURL(appendQuery(query, location.href));
}

export function navigationBack() {
    return navigation.back();
}

export function navigationReload(url) {
    navigation.reload(url);
}
