import React, { useContext } from 'react';
import { SimpleEmage } from '@pdd/lazy-image';
import { generateCDNImageUrl } from '@pdd/core-util';
import { PDDContext } from 'src/framework';
import WebpContext from 'src/framework/Providers/WebpContext';

interface IEmageWrapProps {
    src: string;
    // 是否作为背景图，只有非lazy的时候生效
    asBg?: boolean;
    bgStyle?: React.CSSProperties;
    lazy?: boolean;
    resizeWidth?: number;
    webpEnable?: boolean;
    className?: string;
    plainWatermark?: string;
    style?: React.CSSProperties;
    onClick?(): void;
    preload?: boolean;
    prefitWidth?: number | boolean;
}

const DEFAULT_PARAM = {
    resizeWidth: 1300,
    quality: 80,
    strip: true,
};

export default function EmageWrap({
    src = '',
    lazy = false,
    className,
    style,
    onClick,
    preload,
    asBg,
    bgStyle,
    resizeWidth,
    ...others
}: IEmageWrapProps) {
    const webpEnable = useContext(WebpContext);
    const ctx = useContext(PDDContext);
    const processParam = {
        ...others,
        resizeWidth,
        webpEnable: others.webpEnable || webpEnable,
        enableGifToWebp: !ctx.isIOSSystem,
    };
    if (!process.env.BROWSER && preload && !lazy && ctx.res && ctx.res.locals.preloadAssetsController) {
        ctx.res.locals.preloadAssetsController.addImage(src, others);
    }
    if (!src) {
        return null;
    }

    if (!lazy && asBg) {
        return (
            <div
                className={className}
                onClick={onClick}
                {...others}
                style={{
                    display: 'inline-block',
                    backgroundImage: `url(${generateCDNImageUrl(src, { ...DEFAULT_PARAM, ...processParam })})`,
                    backgroundPosition: 'top',
                    backgroundRepeat: 'no-repeat',
                    backgroundSize: '100% auto',
                    ...bgStyle,
                    ...style,
                }}
            />
        );
    }

    return (
        <SimpleEmage
            src={src}
            processParam={processParam}
            lazy={lazy}
            className={className}
            style={style}
            onClick={onClick}
        />
    );
}
