import React, { useContext } from 'react';
import { generateCDNImageUrl } from '@pdd/core-util';
import WebpContext from 'src/framework/Providers/WebpContext';

type IProps = {
    backgroundImage: string;
    children?: React.ReactNode | React.ReactNode[];
    imgParams?: {
        [k: string]: any;
    };
    [k: string]: any;
} & React.DetailedHTMLProps<React.HTMLAttributes<HTMLDivElement>, HTMLDivElement>;

const Div = (props: IProps) => {
    const { backgroundImage, children, style = {}, imgParams = {}, ...rest } = props;
    const webpEnable = useContext(WebpContext);
    const processBgImage = `url(${generateCDNImageUrl(backgroundImage, {
        ...imgParams,
        webpEnable,
        resizeWidth: 1300,
    })})`;
    return (
        // eslint-disable-next-line react/jsx-props-no-spreading
        <div
            style={{
                backgroundSize: '100% 100%',
                backgroundRepeat: 'no-repeat',
                ...style,
                backgroundImage: processBgImage,
            }}
            {...rest}
        >
            {children}
        </div>
    );
};

export default Div;

export const getProcessImgUrl = (img, webpEnable, imgParams = {}) => {
    return generateCDNImageUrl(img, {
        ...imgParams,
        webpEnable,
        resizeWidth: 1300,
    });
};
