// 通用生成反爬字段
import { createDefaultAxios } from '@pdd/isomorphic-request';
import getServerTime from './getServerTime';
import RiskController from './RiskController';

class RiskControllerUtil {
    constructor() {
        this.riskController = null;
    }

    async getAntiContent(serverTime) {
        if (process.env.BROWSER) {
            // 反扒
            if (!this.riskController) {
                if (!serverTime) {
                    serverTime = await getServerTime.get(createDefaultAxios({ pageInfo: {} })).catch(() => {});
                }
                this.riskController = new RiskController({
                    serverTime: serverTime || Date.now(),
                    _2827c887a48a351a: false,
                });
                this.riskController.initRiskController();
            }
            const antiContent = await this.riskController.getRiskControlInfoAsync();
            if (antiContent) {
                return encodeURIComponent(antiContent);
            }
        } else {
            return null;
        }
    }

    clearCache() {
        this.riskController && this.riskController.clearCache();
    }
}

export default new RiskControllerUtil();
